<template>
    <el-container>
        <el-header>
            添加水库
        </el-header>
        <el-main>
            <el-row :gutter="15">
                <el-col :span="18">
                    <el-card shadow="hover" v-show="step===1">
                        <el-form label-width="20%">
                            <el-form-item label="水库名称">
                                <el-input
                                        v-model="reservoirName"
                                        placeholder="请输入"
                                        style="width: 60%"></el-input>
                            </el-form-item>
                            <el-form-item label="行政区">
                                <el-select v-model="location"
                                           placeholder="请选择"
                                           filterable>
                                    <el-option
                                            v-for="item in locationList"
                                            :label="item.province"
                                            :value="item.province"
                                            :key="item.province">
                                        {{item.province}}
                                    </el-option>
                                </el-select>
                            </el-form-item>
                            <el-button :disabled="stepOneClickNextDisable" @click="stepOneClickNext">
                                下一步
                            </el-button>
                        </el-form>
                    </el-card>

                    <el-card shadow="hover"
                             class="el-card-step2"
                             v-if="step===2"
                             :body-style="{
                                     flex:'1',
                                     display:'flex',
                                     flexDirection:'column',
                                     padding:'0',
                                 }">
                        <baidu-map
                                class="radar-map"
                                :scroll-wheel-zoom="true"
                                :center="center"
                                :zoom="zoom"
                                :continuous-zoom="true"
                                @moving="stepTwoUpdateMapZoom"
                                @moveend="stepTwoUpdateMapZoom"
                                @zoomend="stepTwoUpdateMapZoom"

                        >
                            <bm-polygon :path="polygonPath" stroke-color="blue" :stroke-opacity="0.3"
                                        :stroke-weight="2"/>
                            <bm-marker
                                    v-for="item in polygonPath"
                                    :position="{lng: item.lng, lat: item.lat}"
                                    :icon="pointer"
                                    :key="item.index"
                                    dragging
                                    @mousedown="changeCurrentMarker(item.index)"
                                    @dragging="handleMarkerMove"
                                    @dragend="handleMarkerMoveEnd"
                            >
                            </bm-marker>
                            <transition name="el-zoom-in-top">
                                <el-card shadow="hover" v-show="selecting&&!dragging" class="el-card-step2-info" :style="movingStyle">
                                    <div>
                                        左上角经度：<span class="el-card-step2-span" v-text="lng_LT"></span>
                                    </div>
                                    <div>
                                        左上角纬度：<span class="el-card-step2-span" v-text="lat_LT"></span>
                                    </div>
                                    <div>
                                        右下角经度：<span class="el-card-step2-span" v-text="lng_RB"></span>
                                    </div>
                                    <div>
                                        右下角纬度：<span class="el-card-step2-span" v-text="lat_RB"></span>
                                    </div>

                                </el-card>
                            </transition>

                            <transition name="el-zoom-in-top">
                                <el-card shadow="hover" class="el-card-step2-switch" :style="movingStyle" v-show="!dragging">
                                    <div>
                                        开启框选：
                                        <el-switch v-model="selecting"/>
                                    </div>

                                </el-card>
                            </transition>
                        </baidu-map>
                        <div :style="{
                                display: 'flex',
                                alignItems: 'center',
                                margin:'10px',
                            }">
                            <el-input v-model="searchText"
                                      placehoder="输入关键字"
                                      style="flex-basis: 200px; margin-right: 0px">

                            </el-input>
                            <el-button style="margin-bottom: 0" @click="handleSearchButtonClick">搜索</el-button>

                            <div style="flex:2">
                                <el-button style="margin-bottom: 0" type="success" :disabled="!selecting" @click="stepTwoClickSave">
                                    确认添加
                                </el-button>
                                <el-button style="margin-bottom: 0" @click="stepTwoClickBack">
                                    上一步
                                </el-button>
                            </div>

                        </div>


                    </el-card>
                    <el-card shadow="hover" v-show="step===3">
                        <i class="el-icon-success"></i>
                        水库已添加
                        <el-button @click="stepThreeClick">
                            返回数据管理
                        </el-button>
                    </el-card>

                </el-col>
                <el-col :span="6">
                    <el-card shadow="hover">
                        <el-steps direction="vertical" :active="step-1">
                            <el-step title="输入信息" description="输入水库名，选择行政区"></el-step>
                            <el-step title="框选水库" description="在地图上确认水库坐标范围"></el-step>
                            <el-step title="添加完成"></el-step>
                        </el-steps>
                    </el-card>
                </el-col>
            </el-row>
        </el-main>
    </el-container>

</template>

<script>
    export default {
        name: "addReservoir",
        data: function () {
            return {
                center:'北京',
                centerPoint: {
                    lng: '',
                    lat: '',
                },
                currentMarkerIndex: '',
                dragging: false,
                reservoirName: '',
                location: '',
                locationList: [
                    {province: '北京'},
                    {province: '天津'},
                    {province: '河北'},
                    {province: '山西'},
                    {province: '内蒙古'},
                    {province: '辽宁'},
                    {province: '吉林'},
                    {province: '黑龙江'},
                    {province: '上海'},
                    {province: '江苏'},
                    {province: '浙江'},
                    {province: '安徽'},
                    {province: '福建'},
                    {province: '江西'},
                    {province: '山东'},
                    {province: '河南'},
                    {province: '湖北'},
                    {province: '湖南'},
                    {province: '广东'},
                    {province: '广西'},
                    {province: '海南'},
                    {province: '重庆'},
                    {province: '四川'},
                    {province: '贵州'},
                    {province: '云南'},
                    {province: '西藏'},
                    {province: '陕西'},
                    {province: '甘肃'},
                    {province: '青海'},
                    {province: '宁夏'},
                    {province: '新疆'},
                    {province: '台湾'},
                    {province: '香港'},
                    {province: '澳门'}
                ],

                moving: false,
                pointer: {
                    url: '/circle.png',
                    size: {width: 20, height: 20},
                },
                polygonPath: [],
                selecting: false,
                step: 1,
                searchText: '',
                timeout: {},
                zoom: 12,
            }
        },
        computed: {
            movingStyle: function () {
                if (this.moving) {
                    return {
                        opacity: 0.3
                    }
                } else {
                    return {opacity: 1};
                }
            },
            lng_LT: function () {
                return this.polygonPath.length === 0 ? '-' : this.polygonPath[0].lng;
            },
            lat_LT: function () {
                return this.polygonPath.length === 0 ? '-' : this.polygonPath[0].lat;
            },
            lng_RB: function () {
                return this.polygonPath.length === 0 ? '-' : this.polygonPath[2].lng;
            },
            lat_RB: function () {
                return this.polygonPath.length === 0 ? '-' : this.polygonPath[2].lat;
            },
            stepOneClickNextDisable(){
                return this.reservoirName===''||this.location==='';
            }
        },
        watch: {
            selecting: function (newValue) {
                if (newValue === true) {
                    this.getPathFromCenterPoint(0.05, 0.04);
                } else {
                    this.polygonPath = [];
                }
            }
        },
        methods: {
            stepOneClickNext: function () {
                if (this.reservoirName===''||this.location===''){
                    console.log('未输入');
                }
                else {
                    this.center = this.location;
                    this.searchText = this.reservoirName;
                    this.step++;
                }
            },
            stepTwoClickBack: function () {
                this.step--
            },
            stepTwoUpdateMapZoom: function(e) {
                const self = this;
                this.moving = true;
                clearTimeout(this.timeout);
                this.timeout = setTimeout(function () {
                    self.moving = false;
                }, 500);
                const c = e.target.getCenter();
                this.centerPoint.lng = c.lng;
                this.centerPoint.lat = c.lat;
                this.zoom = e.target.getZoom()
            },
            stepTwoClickSave:function () {
                const self = this;
                this.axios.get('/api/addReservoir', {
                    params:{
                        name: self.reservoirName,
                        location: self.location,
                        longitudeLeft: self.lng_LT,
                        latitudeLeft: self.lat_LT,
                        longitudeRight: self.lng_RB,
                        latitudeRight: self.lat_RB,
                    }
                }).then((response)=>{
                    console.log(response.data);
                    self.step++;
                })
            },
            getPathFromCenterPoint: function (lngNum, latNum) {
                const point = this.centerPoint;
                this.polygonPath = [
                    {index: 0, lng: (point.lng - lngNum).toFixed(6), lat: (point.lat + latNum).toFixed(6)},
                    {index: 1, lng: (point.lng + lngNum).toFixed(6), lat: (point.lat + latNum).toFixed(6)},
                    {index: 2, lng: (point.lng + lngNum).toFixed(6), lat: (point.lat - latNum).toFixed(6)},
                    {index: 3, lng: (point.lng - lngNum).toFixed(6), lat: (point.lat - latNum).toFixed(6)},
                ]
            },
            changeCurrentMarker: function (index) {
                this.currentMarkerIndex = index;
            },
            handleMarkerMove: function (e) {
                this.dragging = true;
                const path = this.polygonPath;
                const lng = e.point.lng;
                const lat = e.point.lat;
                switch (this.currentMarkerIndex) {
                    case 0: {
                        const lngLimit = path[2].lng;
                        const latLimit = path[2].lat;
                        path[0].lng = Math.min(lng, lngLimit);
                        path[0].lat = Math.max(lat, latLimit);
                        path[1].lat = Math.max(lat, latLimit);
                        path[3].lng = Math.min(lng, lngLimit);
                        break;
                    }
                    case 1: {
                        const lngLimit = path[3].lng;
                        const latLimit = path[3].lat;
                        path[1].lng = Math.max(lng, lngLimit);
                        path[1].lat = Math.max(lat, latLimit);
                        path[0].lat = Math.max(lat, latLimit);
                        path[2].lng = Math.max(lng, lngLimit);
                        break;
                    }
                    case 2: {
                        const lngLimit = path[0].lng;
                        const latLimit = path[0].lat;
                        path[2].lng = Math.max(lng, lngLimit);
                        path[2].lat = Math.min(lat, latLimit);
                        path[3].lat = Math.min(lat, latLimit);
                        path[1].lng = Math.max(lng, lngLimit);
                        break;
                    }
                    case 3: {
                        const lngLimit = path[1].lng;
                        const latLimit = path[1].lat;
                        path[3].lng = Math.min(lng, lngLimit);
                        path[3].lat = Math.min(lat, latLimit);
                        path[2].lat = Math.min(lat, latLimit);
                        path[0].lng = Math.min(lng, lngLimit);
                        break;
                    }
                    default: {
                        break
                    }
                }
            },
            handleMarkerMoveEnd: function () {
                this.dragging = false;
            },
            handleSearchButtonClick: function () {
                this.center = this.searchText;
            },
            stepThreeClick:function () {
                this.$router.go(-1);
            }
        },
    }
</script>

<style scoped>
    .el-container {
        min-height: 100%;
    }

    .el-main {
        height: 100%;
        display: inline-flex;
        padding: 0 5px 0 20px;
    }

    .el-row {
        width: 100%;
        overflow: hidden;
    }

    .el-col-18 {
        height: 100%;
        overflow-y: auto;
        display: flex;
        flex-direction: column;
    }

    .el-col-18::-webkit-scrollbar {
        display: none;
    }

    .el-progress {
        float: right;
        width: 50%;
        margin-top: 10px;
    }


    .el-button {
        float: right;
        margin-bottom: 20px;
        margin-left: 20px;
    }

    .el-card {
        border: 1px solid #d4d4d4;
        margin-top: 20px;
        margin-bottom: 20px;
    }

    .el-steps {
        height: 200px;
    }

    .el-card-step2 {
        flex: 1;
        display: flex;
    }

    .el-card-step2-info {
        position: absolute;
        top: 10px;
        right: 10px;
        margin: 0;
        width: auto;
        transition: .3s;
    }

    .el-card-step2-switch {
        position: absolute;
        top: 10px;
        left: 10px;
        margin: 0;
        width: auto;
        transition: .3s;
    }

    .el-card-step2-info-moving {
        opacity: 0.3;
    }

    .radar-map {
        flex: 1;
        position: relative;
    }
</style>
